// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 保养项配置组件
import maintanceTypeConfig from '@/views/main/MaintainceManagement/MaintanceTypeConfig.vue';
// 保养场景组件
import maintanceCaseConfig from '@/views/main/MaintainceManagement/MaintanceCaseConfig.vue';
// 保养模板配置组件
import maintanceTemplateConfig from '@/views/main/MaintainceManagement/MaintanceTemplateConfig.vue';
// 设备类型保养配置
import maintanceLinkToolConfig from '@/views/main/MaintainceManagement/MaintanceLinkToolConfig.vue';
// 设备类型保养配置
import maintanceReleUser from '@/views/main/MaintainceManagement/maintanceReleUser.vue';
// 文字常量
import { PAGE_TITLE } from '@/utils/constantsFile';
export default {
  name: 'MaintainceManagement',
  components: {
    HeaderTitle,
    maintanceTypeConfig,
    maintanceCaseConfig,
    maintanceTemplateConfig,
    maintanceLinkToolConfig,
    maintanceReleUser
  },
  data() {
    return {
      PAGE_TITLE: PAGE_TITLE,
      // 标题
      title: PAGE_TITLE.MAINTANCE_MANAGEMENT,
      // 激活的tab页
      activeName: 'first',
      // 要显示tab
      tabID: 1
    };
  },
  methods: {
    /**
     * @description: 控制tab切换
     * @param {Object} tab tab属性
     * @param {Object} event 触发事件*
     * @return: void
    */
    handleClick(tab, event) {
      console.log(tab);
      if (tab.label === PAGE_TITLE.MAINTANCE_MANAGEMENT_ITEMCONFIG) {
        this.tabID = 1;
      } else if (tab.label === PAGE_TITLE.MAINTANCE_CYCLE) {
        this.tabID = 2;
      } else if (tab.label === PAGE_TITLE.MAINTANCE_MANAGEMENT_TEMPLATECONFIG) {
        this.tabID = 3;
      } else if (tab.label === PAGE_TITLE.MAINTANCE_MANAGEMENT_TOOL_RELATION) {
        this.tabID = 4;
      } else if (tab.label === '用户关联') {
        this.tabID = 5;
      }
    }
  }
};
