<template>
  <el-dialog
    class="create-time-option"
    width="50%"
    :title="title"
    :visible.sync="showDialog"
    :close-on-click-modal="false"
    @closed="closeAction"
  >
    <div class="content" style="height: 450px;" >
        <el-form :model="modalData" :rules="rules" ref="ruleForm" label-width="100px" class="ruleForm">
          <el-form-item :label='EQUIPMENT_DATA.CYCLE_NAME' prop="cycleName" class="content-label">
            <el-input v-model="modalData.cycleName" ></el-input>
          </el-form-item>
          <el-form-item :label="'提醒天数'" class="content-label">
            <el-input type="number" v-model="modalData.remindDayCount" ></el-input>
          </el-form-item>
          <el-form-item :label='EQUIPMENT_DATA.STARTDATE' prop="startTime" class="content-label">
            <el-date-picker
                    class="selectplace"
                    v-model="modalData.startTime"
                    type="datetime"
                    :placeholder="EQUIPMENT_DATA.PLESE_SELECT_DATE">
            </el-date-picker>
          </el-form-item>
          <el-form-item :label='EQUIPMENT_DATA.CYCLE_TYPE' prop="cycleType" class="content-label is-required">
            <el-select class="selectplace" v-model="modalData.cycleType" :placeholder="MSG.PLEASE_CHOOSE" @change="validateFileds()">
              <el-option
                  v-for="item in $store.state.timeStatus"
                  :key="item.type"
                  :label="item.text"
                  :value="item.type">
              </el-option>
            </el-select>
            <div class="content-detail">
              <div class="timescope">
                <div v-if="modalData.cycleType==1" class="day">
                  {{EQUIPMENT_DATA.EACH}}:<el-input-number class="num" v-model="modalData.typeSeparate" :min="0" :max="365"></el-input-number>{{EQUIPMENT_DATA.HAPPEN_ONCE}}
                </div>
                <div v-if="modalData.cycleType==2" class="week">
                  {{EQUIPMENT_DATA.EACH}}:<el-input-number class="num" v-model="modalData.typeSeparate" :min="0" :max="60"></el-input-number>{{MAINTAINCE_CYCLE_TYPE['2']}}:
                  <el-radio-group class="weekday" v-model="modalData.weekDay" >
                    <el-radio label=1 >{{WEEK_DAY['1']}}</el-radio>
                    <el-radio label=2 >{{WEEK_DAY['2']}}</el-radio>
                    <el-radio label=3 >{{WEEK_DAY['3']}}</el-radio>
                    <el-radio label=4 >{{WEEK_DAY['4']}}</el-radio>
                    <el-radio label=5 >{{WEEK_DAY['5']}}</el-radio>
                    <el-radio label=6 >{{WEEK_DAY['6']}}</el-radio>
                    <el-radio label=7 >{{WEEK_DAY['7']}}</el-radio>
                  </el-radio-group>
                </div>
                <div v-if="modalData.cycleType==3" class="month">
                  {{EQUIPMENT_DATA.EACH}}:<el-input-number class="num" v-model="modalData.typeSeparate" :min="0" :max="12" label="描述文字"></el-input-number>{{EQUIPMENT_DATA.HAPPEN_ONCE_MONTH}}
                </div>
                <div v-if="modalData.cycleType==4" class="halfyear">
                  <!-- <el-input-number class="num" v-model="halfyear" :min="1" :max="1" label="描述文字"></el-input-number> -->
                  <div>{{EQUIPMENT_DATA.EACH}}:{{EQUIPMENT_DATA.HAPPEN_ONCE_HALFYEAR}}</div>
                </div>
                <div v-if="modalData.cycleType==5" class="year">
                  {{EQUIPMENT_DATA.EACH}}:<el-input-number class="num" v-model="modalData.typeSeparate" :min="0" :max="365" label="描述文字"></el-input-number>{{EQUIPMENT_DATA.HAPPEN_ONCE_YEAR}}
                </div>
              </div>
            </div>
          </el-form-item>
          <el-form-item :label='EQUIPMENT_LIVE.REMARKS' prop="cycleRemark" class="content-label">
            <el-input v-model="modalData.cycleRemark" type="textarea" :rows="3" :placeholder="MSG.PLEASE_ENTER_CONTENT"></el-input>
          </el-form-item>
        </el-form>
    </div>
    <div slot="footer">
      <el-button type="primary" @click="confirmAction('ruleForm')">{{confirmLabel}}</el-button>
      <el-button type="tertiary" @click="cancelAction">{{cancelLabel}}</el-button>
    </div>
  </el-dialog>
</template>
<script>
import { EQUIPMENT_DATA, MSG, BTN_TEXT, MAINTAINCE_CYCLE_TYPE, WEEK_DAY, EQUIPMENT_LIVE, CONSTANTS_CODE } from '@/utils/constantsFile.js';
import moment from 'moment';
export default {
  name: 'CreateMaintainceOptionModal',
  props: {
    // 模态框名字
    title: { type: String, default: '' },
    // 取消标签
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确认标签
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 显示弹窗
    showDialog: { type: Boolean, default: false },
    // 是添加操作还是修改操作
    isAdd: { type: Boolean, default: true },
    // 数据
    data: { type: Object, default: () => {} },
    // 处理点击取消
    handleCancel: Function,
    // 处理点击确认
    handleConfirm: Function,
    // 处理点击关闭
    handleClose: Function
  },
  data() {
    const validateAcquaintance = (data) => {
      return new Promise((resolve, reject) => {
        if (!this.modalData[data.field]) {
          reject(new Error(EQUIPMENT_DATA.PLEASE_ENTER_CYCLENAME));
        } else {
          resolve();
        }
      });
    };
    return {
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      WEEK_DAY: WEEK_DAY,
      MAINTAINCE_CYCLE_TYPE: MAINTAINCE_CYCLE_TYPE,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      MSG: MSG,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_CODE: CONSTANTS_CODE,
      modalData: {
        // 周期id
        cycleId: null,
        // 提醒天数
        remindDayCount: null,
        // 周期名字
        cycleName: null,
        // 周期备注
        cycleRemark: null,
        // 开始时间
        startTime: null,
        // 周期类型
        cycleType: null,
        // 星期数组
        weekDay: [],
        // 间隔时间
        typeSeparate: null
      },
      rules: {
        cycleName: [
          { required: true, message: EQUIPMENT_DATA.PLEASE_ENTER_CYCLENAME, trigger: 'blur' }
        ],
        startTime: [
          { required: true, message: EQUIPMENT_DATA.PLEASE_ENTER_CYCLENAME, trigger: 'blur' }
        ],
        cycleType: [
          { validator: validateAcquaintance, trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    /**
     * @description: 初始化数据
     * @return: void
    */
    initData() {
      if (this.data) {
        this.modalData.cycleName = this.data.cycleName;
        this.modalData.remindDayCount = this.data.remindDayCount;
        this.modalData.startTime = this.data.startTime;
        this.modalData.cycleType = this.data.cycleType;
        this.modalData.typeSeparate = this.data.typeSeparate;
        this.modalData.cycleRemark = this.data.cycleRemark;
        if (this.data.cycleType === 2 && this.data.typeWeekCheckWeekDay) {
          this.modalData.weekDay = this.data.typeWeekCheckWeekDay;
        }
        this.modalData.cycleId = this.data.cycleId || null;
      }
    },
    /**
     * @description: 处理点击取消
     * @return: void
    */
    cancelAction: function() {
      if (this.handleCancel) {
        this.handleCancel();
      }
      this.clearData();
    },
    validateFileds() {
      this.$refs.ruleForm.validateField('cycleType');
      if (this.modalData.cycleType === 2) {
        this.modalData.weekDay = '1';
      }
      if (this.modalData.cycleType === 3) {
        this.modalData.typeSeparate = 1;
      }
    },
    /**
     * @description: 处理点击确认
     * @return: void
    */
    confirmAction(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.handleConfirm) {
            var data = {
              isAdd: this.isAdd,
              data: {
                cycleName: this.modalData.cycleName,
                remindDayCount: this.modalData.remindDayCount,
                cycleRemark: this.modalData.cycleRemark,
                startTime: this.modalData.startTime = this.modalData.startTime ? moment(this.modalData.startTime).format('YYYY-MM-DD HH:mm:ss') : null,
                cycleType: this.modalData.cycleType,
                typeSeparate: this.modalData.typeSeparate,
                typeWeekCheckWeekDay: this.modalData.weekDay ? this.modalData.weekDay : '',
                cycleId: this.modalData.cycleId
              }
            };
            this.handleConfirm(data);
            this.clearData();
          }
        } else {
          // showErrorNotify(CONSTANTS_CODE.PLEASE_ENTER_CONTENT);
          return false;
        }
      });
    },
    /**
     * @description: 清空数据
     * @return: void
    */
    clearData() {
      this.modalData.remindDayCount = '';
      this.modalData.cycleName = null;
      this.modalData.startTime = null;
      this.modalData.cycleType = null;
      this.modalData.typeSeparate = null;
      this.modalData.cycleRemark = null;
      this.modalData.weekDay = [];
    },
    /**
     * @description: 处理点击关闭
     * @return: void
    */
    closeAction: function() {
      if (this.handleClose) {
        this.handleClose();
      }
      this.clearData();
    }
  },
  watch: {
    data(val, oldVal) {
      this.initData();
    }
  }
};
</script>
