<template>
  <div>
     <el-dialog
    class="bind-maintaince-template"
    width="800px"
    :title="title"
    :visible.sync="showDialog"
    :close-on-click-modal="false"
    @closed="closeAction"
  >
    <div class="content">
      <div style="text-align:left;margin-bottom:10px;" >
        <div style="margin-bottom:10px;" >
          <span class="demonstration">选择年份：</span>
          <el-date-picker
            size="small"
            v-model="year"
            type="year"
            placeholder="选择年"
            value-format="yyyy"
            @change="dateFaram"
          >
          </el-date-picker>
          <span style="margin-left:10px;">添加日期：</span>
          <el-date-picker
            size="small"
            v-model="value1"
            type="date"
            value-format="yyyy-MM-dd HH:hh:mm"
            placeholder="选择日期">
          </el-date-picker>
          <el-button style="margin-left:10px;" size="mini" type="primary" icon="el-icon-plus" circle @click="addTime()" ></el-button>
        </div>
        <span class="display-flex">
          <el-link
              class="width-120 margin-right-10"
              :underline="false"
              @click="exportTemplate"
            >保养计划导入模版.xls</el-link>
            <el-input size="small" class="margin-right-10" v-model="importFileName" disabled></el-input>
            <el-upload
              action
              class="display-flex file-upload"
              :auto-upload="false"
              accept=".xlsx, .xls"
              ref="upload"
              :http-request="submitFile"
              :file-list="importFiles"
              :show-file-list="false"
              :on-change="selectFile"
              :on-success="handleUploadSuccess"
              :on-error="handleUploadError"
            >
              <el-button size="small" slot="trigger" class="margin-right-10">{{BTN_TEXT.CHOOSE_FILE}}</el-button>
              <el-button
                size="small"
                type="primary"
                class="custom-button"
                @click="uploadBtnClick"
                :disabled="importFiles.length===0"
              >{{BTN_TEXT.IMPORT}}</el-button>
            </el-upload>
            <el-button size="small" slot="primary" class="margin-right-10" @click="getList()" >刷新</el-button>
        </span>
        <span class="display-flex margin-top-15" v-show="isUploadProgressShow">
          <span>{{VARIABLE.UPLOAD_PROGRESS}}：</span>
          <el-progress
            :text-inside="true"
            :percentage="uploadPercent"
            :stroke-width="22"
            :format="formatText"
            :color="uploadProgressColor"
          ></el-progress>
        </span>
        <div class="errorDiv" v-if="errorList.length>0">
          <div class="errorTitle">{{VARIABLE.ERROR_INFO}}:{{errorList.length}}{{VARIABLE.ARTICLE}}</div>
          <div class="errorListDiv" >
            <el-alert
              :title=" item.failData + '  ' + item.failReason "
              v-for="(item,index) in errorList"
              :key="index"
              type="error"
              show-icon
            ></el-alert>
          </div>
        </div>
        <div style="color: red;" >
          <span>导入年份必填，格式例如：2023</span>
          <br/>
          <span>导入月份格式例如：1.5;1.23</span>
        </div>
        </div>
          <VXETable
            id="table"
            :resizable="true"
            :autoResize="true"
            :tableName="'user'"
            :height="400"
            :tableLoading="tableLoading"
            :pager-config="tablePage"
            :tableDatas="tableDatas"
            :tableColumns="tableColumns"
            :tablePage="tablePage"
            ref="xGrid"
          >
          </VXETable>
    </div>
    <div slot="footer">
      <el-button type="primary" @click="submitData">保存</el-button>
      <el-button type="tertiary" @click="cancelAction">关闭</el-button>
    </div>
  </el-dialog>
  </div>
</template>
<script>
import VXETable from '@/components/common/Table';
import { showSuccessMessage, showWarningMessage, showErrorMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { VARIABLE, STATUS_CODE, TITLE, BTN_TEXT, MSG } from '@/utils/constantsFile';
// import { BASE_URL } from '@/utils/ajaxConfigure';
import {
  exportMaintainPlanTemplate
} from '@/api/export';
import {
  importMaintainPlan
} from '@/api/import';
import {
  getDeviceMainPlanList,
  insertOrUpdateMaintainPlan,
  maintainPlanDeleteById
} from '@/api/maintanceManagement';
export default {
  name: 'bindMaintainceTemplateModal',
  components: {
    VXETable
  },
  props: {
    // 模态框名字
    title: { type: String, default: '保养任务周期设置' },
    companyId: { type: Number, default: 0 },
    // 取消标签
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确认标签
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 显示弹窗
    showDialog: { type: Boolean, default: false },
    // 数据
    itemsList: { type: Array, default: () => [] },
    ztreeSelectedNode: { type: Object, default: () => {} },
    // 处理点击取消
    handleCancel: Function,
    // 处理点击确认
    handleConfirm: Function,
    // 处理点击关闭
    handleClose: Function
  },
  data() {
    return {
      // CONSTANTS_CHECK: CONSTANTS_CHECK,
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      year: '',
      value1: '',
      tableLoading: false,
      importFileName: '',
      importFiles: [],
      // 上传进度条进度值
      uploadPercent: 0,
      // 进度条是否显示
      isUploadProgressShow: false,
      uploadProgressColor: '#0871dc',
      errorList: [],
      isShow: false,
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [5, 10, 20, 30, 50],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      tableDatas: [],
      tableColumns: [
        {
          field: 'planTime',
          title: '时间列表',
          visible: true,
          minWidth: '100',
          align: 'center',
          formatter: ({ row, column, cellValue }) => {
            return row.planTime.substring(0, 11);
          }
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          visible: true,
          width: '200',
          align: 'center',
          fixed: 'right',
          slots: {
            default: ({ row, column, cellValue }) => {
              return [
                <div>
                  <el-button size="mini" type="danger" onClick={() => this.remove(row)}>删除</el-button>
                </div>
              ];
            }
          }
        }
      ],
      downloadSrc: '',
      fileList: [],
      uploadTime: 0,
      canUpload: false
    };
  },
  created() {
    this.year = new Date().getFullYear() + '';
    this.getList();
  },
  methods: {
    init() {
      this.isUploadProgressShow = false;
      this.uploadPercent = 0;
      this.importFileName = '';
      this.importFiles = [];
      this.errorList = [];
      this.year = new Date().getFullYear() + '';
      this.value1 = '';
      this.getList();
    },
    /**
     * @description: 处理点击取消
     * @return: void
     */
    cancelAction: function() {
      if (this.handleCancel) {
        this.handleCancel();
      }
    },
    /**
     * @description: 处理点击确认
     * @return: void
     */
    confirmAction: function() {
      this.handleConfirm(this.value);
    },
    /**
     * @description: 处理点击关闭
     * @return: void
     */
    closeAction: function() {
      // if (this.handleClose) {
      //   this.handleClose();
      // }
      if (this.handleCancel) {
        this.handleCancel();
      }
    },
    dateFaram(param) {
      this.year = param;
      this.getList();
    },
    addTime() {
      console.log(this.value1);
      if (this.value1) {
        if (this.tableDatas.length > 0) {
          var flag = 0;
          var time = this.value1.substring(0, 11);
          for (var i = 0; i < this.tableDatas.length; i++) {
            console.log(this.tableDatas[i].planTime, time);
            if (this.tableDatas[i].planTime.indexOf(time) > -1) {
              flag = 1;
            }
          }
          if (flag === 1) {
            showWarningMessage('重复日期');
          } else {
            this.tableDatas.push({
              cycleId: this.$store.state.templateRow.cycleId,
              deviceCode: this.$store.state.templateDevCode,
              templateId: this.$store.state.templateRow.templateId,
              planTime: this.value1
            });
          }
        } else {
          this.tableDatas.push({
            cycleId: this.$store.state.templateRow.cycleId,
            deviceCode: this.$store.state.templateDevCode,
            templateId: this.$store.state.templateRow.templateId,
            planTime: this.value1
          });
        }
      }
    },
    async getList() {
      this.tableDatas = [];
      this.tablePage.total = 0;
      this.tableLoading = true;
      try {
        const res = await getDeviceMainPlanList({
          cycleId: this.$store.state.templateRow.cycleId,
          deviceCode: this.$store.state.templateDevCode,
          templateId: this.$store.state.templateRow.templateId,
          year: this.year
        }, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
        } else {
          // 提示异常信息
          // showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    async submitData() {
      console.log(this.tableDatas);

      try {
        var arr = [];
        for (var i = 0; i < this.tableDatas.length; i++) {
          var json = {
            cycleId: this.tableDatas[i].cycleId,
            deviceCode: this.tableDatas[i].deviceCode,
            templateId: this.tableDatas[i].templateId,
            planTime: this.tableDatas[i].planTime.substring(0, 11) + '00:00:00'
          };
          if (this.tableDatas[i].id) {
            json.id = this.tableDatas[i].id;
          }
          arr.push(json);
        }
        console.log(arr);

        const res = await insertOrUpdateMaintainPlan({}, arr);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // this.tableDatas = res.rows;
          // this.tablePage.total = res.total;
          showSuccessMessage(res.message);
          this.getList();
          // setTimeout(() => {
          //   this.handleCancel();
          // }, 300);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    async remove(row) {
      if (row.id) {
        this.tableLoading = true;
        try {
          const res = await maintainPlanDeleteById({
            id: row.id
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.getList();
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
        } finally {
          this.tableLoading = false;
        }
      } else {
        var index = 0;
        for (var i = 0; i < this.tableDatas.length; i++) {
          if (this.tableDatas[i].id === row.id) {
            index = i;
          }
        }
        console.log(index);
        this.tableDatas.splice(index, 1);
      }
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTemplate() {
      try {
        const res = await exportMaintainPlanTemplate();
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          this.$options.filters.setExportFunc(res, '保养计划导入模版.xls');
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    submitFile(param) {
      this.isUploadProgressShow = false;
      this.uploadPercent = 0;
      this.errorList = [];
      const fileObj = param.file;
      const fd = new FormData();
      fd.append('file', fileObj);
      // 执行接口
      importMaintainPlan({}, fd, {
        onUploadProgress: event => {
          // eslint-disable-next-line prefer-const
          let uploadedPercentage = Math.floor(
            (event.loaded * 100) / event.total
          );
          // option.onProgress({ percent: uploadedPercentage });
          this.uploadPercent = uploadedPercentage;
        }
      })
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            param.onSuccess(res);
            this.getList();
          } else {
            param.onError(res);
            this.getList();
          }
        })
        .catch(err => {
          showWarningMessage(err);
          param.onError(err);
          this.getList();
        });
    },
    selectFile(file, fileList) {
      // 将选中文件的名称写入input
      this.importFileName = file.name;
      this.importFiles = fileList;
    },
    /**
     * @description: （导入用户对话框）文件上传成功的回调函数
     * @param 无
     * @return: void
     * @author: ZJM
     */
    handleUploadSuccess(res) {
      this.isShow = false;
      showSuccessMessage(MSG.IMPORT_SUCCESS);
      // 刷新表格
      // this.$emit('closeImportDialog');
      this.getList();
    },
    /**
     * @description: （导入用户对话框）文件上传失败的回调函数
     * @param 无
     * @return: void
     * @author: ZJM
     */
    // eslint-disable-next-line handle-callback-err
    handleUploadError(err) {
      this.isUploadProgressShow = false;
      this.uploadPercent = 0;
      this.$nextTick(() => {
        this.importFileName = '';
      });
      this.importFiles = [];
      // this.resetUploadProgressBar();
      // // this.resetUploadProgressBar();
      // // this.isImportDialogShow = false;
      showErrorMessage(MSG.IMPORT_ERROR);
      this.errorList = err.rows;
    },
    uploadBtnClick() {
      this.$refs.upload.submit();
      this.uploadPercent = 0;
      // this.isUploadProgressShow = true;
    },
    /**
     * @description: 格式化进度条显示的文字
     * @param {number} percentage 进度百分比
     * @return: void
     * @author: ZJM
     */
    formatText(percentage) {
      return `${percentage}%`;
    },
    /**
     * @description: 重置进度条
     * @param 无
     * @return: void
     * @author: ZJM
     */
    resetUploadProgressBar() {
      this.isUploadProgressShow = false;
      this.uploadPercent = 0;
      // this.$refs.upload.clearFiles();
      this.$nextTick(() => {
        this.importFileName = '';
      });
      // 清空文件列表
      this.importFileName = '';
      this.errorList = [];
    }
  }
};
</script>
<style lang="less"  scoped >
