  padding: 10px 10px !important;
}

// 进度条样式
/deep/ .el-progress-bar__outer {
  width: 100%;
}

/deep/ .file-upload {
  width: 180px;
  display: inline-block;
}

.errorTitle {
  padding: 0;
  margin: 0;
  height: 40px;
  line-height: 40px;
  font-weight: bold;
  font-size: 20px;
  margin-top: 10px;
}
.el-alert {
  min-height: 25px;
  margin-bottom: 5px;
}
.el-alert /deep/ .el-alert__content{
    min-height: 35px;
    /* line-height: 29px; */
    display: contents;
    /* padding-left: 10px; */
    margin-left: 10px;
}
.el-alert /deep/ .el-alert__title {
    font-size: 13px;
    line-height: 20px;
    text-align: left;
}
.el-alert /deep/ .el-alert__icon {
    font-size: 16px;
    margin-right: 10px;
    width: 16px;
}
