    /**
     * @description: 身份证号校验
     * @param {string} rule 校验规则
     * @param {string} value 输入
     * @param {function} callback 回调函数
     * @return: void
     * @author: ZJM
     */
    isIdCardValid(rule, value, callback) {
      if (value) {
        if (
          !/^[1-9]\d{5}(19|20)\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/.test(
            value
          ) &&
          !/^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$/.test(
            value
          )
        ) {
          callback(new Error('身份证号格式错误！'));
        } else {
          callback();
        }
      } else {
        callback();
      }
    },
    change() {
      this.$forceUpdate();
    },
    /**
     * @description: 显示扩展字段列表头
     * @return: void
     * @author: ZJM
     */
    async setTableColume() {
      var that = this;
      try {
        const res = await extendPropertyGetListAction(
          {},
          {
            conditionJson1: [
              {
                column: 'extendType',
                conditionJson1: [],
                conditionJson2: [],
                current: 0,
                operator: 1,
                operatorGroup: 0,
                orderBy: {},
                ptype: 'int',
                size: 0,
                type: 'eq',
                value: 4
              }
            ]
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.extendPropertyValueVoList = res.rows;
          this.tableColumns = [];
          this.tableColumns_head = this.tableColumns_old.slice(
            0,
            this.tableColumns_old.length - 1
          );
          this.tableColumns_operation = this.tableColumns_old[
            this.tableColumns_old.length - 1
          ];
          this.tableColumns = this.tableColumns.concat(this.tableColumns_head);
          if (this.extendPropertyValueVoList.length > 0) {
            const arr = this.extendPropertyValueVoList;
            for (var i = 0; i < arr.length; i++) {
              this.tableColumns.push({
                field: arr[i].propertyCode,
                title: arr[i].extendName,
                width: '160',
                align: 'center',
                visible: true,
                eType: true,
                formatter: this.$options.filters.formatterExtendValue
              });
            }
          }
          this.tableColumns = this.tableColumns.concat(
            this.tableColumns_operation
          );
          this.tableColumns_old2 = this.tableColumns;
          setTimeout(() => {
            that.getCustomColumn(1);
          }, 0);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 保存扩展字段数据
     * @return: void
     * @author: ZJM
     */
    async saveExtendValue() {
      var arr = this.extendPropertyValueVoList.map((item) => {
        if (item.extendValue === null) {
          item.extendValue = '';
        }
        return item;
      });

      try {
        const res = await extendPropertyValueInsertOrUpdateBatchAction({}, arr);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.findTableList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 表单中扩展字段处理
     * @return: void
     * @author: ZJM
     */
    toExtendRulesAndShow(row) {
      // 保存扩展字段数据
      var arr = row.extendPropertyValueVoList.map((item) => {
        item.relationshipTypeId = row.userId;
        return item;
      });
      this.extendPropertyValueVoList = arr;

      this.rules = JSON.parse(JSON.stringify(this.OldRules));
      for (var item in this.extendPropertyValueVoList) {
        // 若类型是日期
        if (this.extendPropertyValueVoList[item].propertyType === 3) {
          this.rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: ['blur', 'change'] }
          ];
        } else {
          // 若类型是字符或数字
          this.rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: 'blur' }
          ];
        }
      }
    },
