    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      var that = this;
      try {
        const params = {
          current: 1,
          size: 99999999
        };

        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: []
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }
        const res = await userGetUserListAction(params, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var customColumns = [];

          // 获取最新表列数据
          if ((Array.isArray(that.savedCustomColumns)) && that.savedCustomColumns.length > 0) {
            customColumns = that.savedCustomColumns;
          } else {
            customColumns = that.tableColumns_old2;
          }

          // 过滤需要展示的列
          var showArr = [];
          var showArrName = [];
          for (var i = 0; i < customColumns.length; i++) {
            if (customColumns[i].type !== 'radio' && customColumns[i].type !== 'checkbox' && customColumns[i].type !== '') {
              showArr.push(customColumns[i].field);
              showArrName.push(customColumns[i].title);
            }
          }

          setTimeout(function() {
            console.log(res.rows, showArr);
            that.$refs.xGrid.exportData('用户列表', res.rows, showArr);

            // const multHeader = [];
            // const header = showArrName;
            // // const filterval = showArr;
            // const data = formatJson(showArr, res.rows);
            // exportJson2Excel({
            //   multHeader,
            //   header,
            //   data,
            //   filename: '用户列表',
            //   merges: []
            // });
          }, 300);
        } else {
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    refreshTable() {
      this.findTableList();
    },
