<template>
  <el-dialog
    class="create-maintaince-option"
    width="30%"
    :title="title"
    :visible.sync="showDialog"
    :close-on-click-modal="false"
    @closed="closeAction"
  >
    <div class="content" style="height: auto">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="ruleForm"
      >
        <el-form-item
          :label="EQUIPMENT_DATA.MAINTANCE_OPTION"
          prop="itemName"
          class="content-label"
        >
          <el-input
            type="textarea"
            :rows="4"
            :placeholder="CONSTANTS_CODE.PLEASE_ENTER_CONTENT"
            v-model="ruleForm.itemName"
          >
          </el-input>
        </el-form-item>
        <el-form-item
          :label="'保养项备注'"
          class="content-label"
        >
          <el-input
            type="textarea"
            :rows="4"
            :placeholder="CONSTANTS_CODE.PLEASE_ENTER_CONTENT"
            v-model="ruleForm.itemRemark"
          >
          </el-input>
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer">
      <el-button type="primary" @click="confirmAction('ruleForm')">{{
        confirmLabel
      }}</el-button>
      <el-button type="tertiary" @click="cancelAction">{{
        cancelLabel
      }}</el-button>
    </div>
  </el-dialog>
</template>
<script>
// 文字常量
import { BTN_TEXT, EQUIPMENT_DATA, CONSTANTS_CODE } from '@/utils/constantsFile.js';
import { showErrorNotify } from '@/utils/notify';
export default {
  name: 'CreateMaintainceOptionModal',
  props: {
    // 模态框名字
    title: { type: String, default: '' },
    // 取消标签
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确认标签
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 显示弹窗
    showDialog: { type: Boolean, default: false },
    // 是添加操作还是修改操作
    isAdd: { type: Boolean, default: true },
    // 数据
    data: { type: Object, default: () => { } },
    // 处理点击取消
    handleCancel: Function,
    // 处理点击确认
    handleConfirm: Function,
    // 处理点击关闭
    handleClose: Function
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      CONSTANTS_CODE: CONSTANTS_CODE,
      // 点检项内容
      ruleForm: {
        itemName: '',
        itemRemark: ''
      },
      rules: {
        itemName: [
          { required: true, message: CONSTANTS_CODE.PLEASE_ENTER_CONTENT, trigger: 'blur' }
        ]
      }
    };
  },
  watch: {
    data(val, oldVal) {
      if (this.data) {
        this.ruleForm.itemName = this.data.maintainItemName;
        this.ruleForm.itemRemark = this.data.maintainItemRemark;
      } else {
        this.ruleForm.itemName = '';
        this.ruleForm.itemRemark = '';
      }
    }
  },
  methods: {
    clearData: function() {
      var self = this;
      setTimeout(() => {
        self.ruleForm.itemName = '';
        self.ruleForm.itemRemark = '';
      });
    },
    /**
     * @description: 处理点击取消
     * @return: void
    */
    cancelAction: function() {
      if (this.handleCancel) {
        this.clearData();
        this.handleCancel();
      }
    },
    /**
     * @description: 处理点击确认
     * @return: void
    */
    confirmAction(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.handleConfirm) {
            var data = {
              isAdd: this.isAdd,
              data: this.ruleForm.itemName,
              data2: this.ruleForm.itemRemark
            };
            if (!this.isAdd) {
              data.id = this.data.maintainItemId;
            }
            this.clearData();
            this.handleConfirm(data);
          }
        } else {
          showErrorNotify(CONSTANTS_CODE.PLEASE_ENTER_CONTENT);
          return false;
        }
      });
    },
    /**
     * @description: 处理点击关闭
     * @return: void
    */
    closeAction: () => {
      if (this.handleClose) {
        this.handleClose();
      }
    }
  }
};
</script>
