// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
// 添加修改周期modal
import CreateTimeModal from '@/components/modal/maintainceOptionModal/createTimeModal';
// 导入常量
import { PAGE_TITLE, BTN_TEXT, EQUIPMENT_DATA, STATUS_CODE, TITLE, PLC_PARAMS, MSG } from '@/utils/constantsFile';
// API
import {
  getAllMaintainceCycle, addMaintainceCycle, updateMaintainceCycleById, deleteMaintainceCycleById
} from '@/api/maintanceManagement.js';
// message service
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
// 提示信息
import { showErrorNotify } from '@/utils/notify';
export default {
  components: {
    HeaderTitle,
    HeadOperationItem,
    VXETable,
    CreateTimeModal
  },
  props: {
  },
  data() {
    return {
      TITLE: TITLE,
      BTN_TEXT: BTN_TEXT,
      PAGE_TITLE: PAGE_TITLE,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      PLC_PARAMS: PLC_PARAMS,
      // 显示添加/修改周期modal
      showCreateTime: {
        show: false,
        data: null
      },
      // 判断添加还是修改标识
      isAddItem: true,
      // 选中的周期
      selectCase: null,
      // 勾选的周期
      selectRecords: [],
      // 主表加载标识
      tableloading: false,
      // 周期列表数据
      maintanceCycleTable: {
        data: [],
        page: {
          total: 10,
          currentPage: 1,
          pageSize: 10,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          slots: { left: 'pager_left' },
          layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total']
        },
        columns: [
          {
            type: 'radio',
            align: 'center',
            visible: true,
            width: '50'
          },
          // { type: 'checkbox', width: '50', align: 'center' },
          {
            field: 'cycleName',
            title: EQUIPMENT_DATA.CYCLE_NAME,
            align: 'center'
          },
          {
            field: 'remindDayCount',
            title: '提醒天数',
            align: 'center'
          },
          {
            field: 'cycleType',
            title: EQUIPMENT_DATA.MAINTANCE_SCOPE,
            align: 'center',
            formatter: this.$options.filters.formatMaintanceCycle
          },
          {
            field: 'maintance_time',
            title: EQUIPMENT_DATA.MAINTANCE_TIME,
            align: 'center',
            formatter: this.$options.filters.formatMaintanceTime
          },
          {
            field: 'cycleRemark',
            title: PLC_PARAMS.REMARK,
            align: 'center'
          }
        ]
      }
    };
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: 获取所有保养场景
     * @param: void
     * @return: void
     * @Author: Lois
     */
    getMaintainCaseData() {
      this.tableloading = true;
      getAllMaintainceCycle({
        current: this.maintanceCycleTable.page.currentPage,
        size: this.maintanceCycleTable.page.pageSize
      }).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableloading = false;
          this.maintanceCycleTable.page.total = res.total;
          this.maintanceCycleTable.data = res.rows;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    /**
     * @description: 打开添加场景对话框
     * @param: {Object} data 某一行数据或者空数据
     * @return: void
     * @Author: Lois
     */
    showCreateTimeModal(data) {
      this.showCreateTime.show = true;
    },
    /**
     * @description: 打开修改场景对话框
     * @param: {Object} data 某一行数据或者空数据
     * @return: void
     * @Author: Lois
     */
    showUpdateTimeModal() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      if (row) {
        this.isAddItem = false;
        this.selectCase = JSON.parse(JSON.stringify(row));
      }
      this.showCreateTime.show = true;
    },
    /**
     * @description: 添加场景对话框中的取消按钮
     * @param: void
     * @return: void
     * @Author: Lois
     */
    clickCreateTimeCancel() {
      this.isAddItem = true;
      this.selectCase = null;
      this.showCreateTime.show = false;
    },
    /**
     * @description: 添加场景对话框中保存后触发API调用
     * @param: {Object} data 对话框数据
     * @return: void
     * @Author: Lois
     */
    addTime(data) {
      if (data.isAdd) {
        addMaintainceCycle(data.data).then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.showCreateTime.show = false;
            this.getMaintainCaseData();
            showSuccessMessage(res.message);
          } else {
            this.showCreateTime.show = false;
            showWarningMessage(res.message);
          }
        }).catch(err => {
          this.showCreateTime.show = false;
          showErrorNotify(err);
        });
      } else {
        updateMaintainceCycleById(data.data).then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.showCreateTime.show = false;
            this.getMaintainCaseData();
            showSuccessMessage(res.message);
          } else {
            this.showCreateTime.show = false;
            showWarningMessage(res.message);
          }
        }).catch(err => {
          this.showCreateTime.show = false;
          showErrorNotify(err);
        });
      }
    },
    /**
     * @description: 控制分页操作
     * @param: {string} currentPage 当前页
     * @param: {string} pageSize 每页显示个数
     * @return: void
     * @Author: Lois
     */
    handleMaintanceCycleTablePageChange(currentPage, pageSize) {
      this.maintanceCycleTable.page.currentPage = currentPage;
      this.maintanceCycleTable.page.pageSize = pageSize;
      this.getMaintainCaseData();
    },
    /**
     * @description: 表格数据多选
     * @param: {Array} records 选择的数据
     * @return: void
     * @Author: Lois
     */
    checkboxChangeEvent(records) {
      this.selectRecords = records;
    },
    toDelete() {
      var row = {};
      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      this.deleteMaintainTimeConfirm([row]);
    },
    /**
     * @description: 删除表格数据
     * @param: {Array} data 选择的数据
     * @return: void
     * @Author: Lois
     */
    deleteMaintainTimeConfirm(data) {
      var deleteItems = [];
      data.forEach(element => {
        deleteItems.push(element.cycleId);
      });
      this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      }).then(() => {
        this.deleteMaintainTime(deleteItems);
      }).catch(() => {
      });
    },
    /**
     * @description: 删除周期
     * @param {Array} deleteItems 要删除的id 数组
     * @return: void
    */
    deleteMaintainTime(deleteItems) {
      deleteMaintainceCycleById({
        ids: deleteItems
      }).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.getMaintainCaseData();
          showSuccessMessage(res.message);
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    refreshTable() {
      this.getMaintainCaseData();
    }
  },
  created() {
    this.getMaintainCaseData();
  }
};
