// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
// 添加/修改保养模板 modal
import createMaintainceTemplateModal from '@/components/modal/maintainceOptionModal/createMaintainceTemplateModal';
// 选择保养项modal
import createMaintainceTemplateRelationModal from '@/components/modal/maintainceOptionModal/createMaintainceTemplateRelationModal';
// 排序组件
import Sortable from 'sortablejs';
// 文字常量
import { BTN_TEXT, EQUIPMENT_DATA, STATUS_CODE, TITLE, PAGE_TITLE, MSG } from '@/utils/constantsFile';
// message service
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
// 提示信息
import { showErrorNotify } from '@/utils/notify';
// API
import {
  getMaintainItem,
  getMaintainTemplate,
  addMaintainTemplate,
  updateMaintainTemplate,
  deleteMaintainTemplate,
  selectMaintainTemplateRelationshipItem,
  updateMaintainTempRelationItem,
  deleteWorkshopTypeRelationshipTemplate,
  selectMaintainLevel
} from '@/api/maintanceManagement.js';
export default {
  components: {
    createMaintainceTemplateModal,
    createMaintainceTemplateRelationModal,
    VXETable,
    HeaderTitle,
    HeadOperationItem
  },
  props: {
  },
  data() {
    return {
      PAGE_TITLE: PAGE_TITLE,
      BTN_TEXT: BTN_TEXT,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      // 添加/修改保养模板 modal 标识
      showCreateMaintainceTemp: false,
      // 主表加载标识
      tableLoading: false,
      // 判断添加还是修改标识
      isAddItem: true,
      // 选中的模板 (for 添加/修改保养模板 modal)
      selectTemplate: null,
      // 勾选的模板
      selectRecords: [],
      // 模板数据列表
      templateTableData: {
        data: [],
        page: {
          total: 10,
          currentPage: 1,
          pageSize: 10,
          pageSizes: [5, 10, 20, 50, 100, 200, 500],
          perfect: true,
          slots: { left: 'pager_left' },
          layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total']
        },
        columns: [
          {
            type: 'radio',
            align: 'center',
            visible: true,
            width: '50'
          },
          // { type: 'checkbox', width: '50', align: 'center' },
          {
            field: 'templateName',
            title: EQUIPMENT_DATA.TEMPLATE_NAME,
            align: 'center'
          },
          {
            field: 'levelId',
            title: EQUIPMENT_DATA.MAINTANCE_LEVEL,
            align: 'center',
            formatter: this.$options.filters.formatMaintanceLevel
          },
          {
            field: 'cycleName',
            title: EQUIPMENT_DATA.MAINTANCE_CYCLE_TEMPLATE_NAME,
            align: 'center'
          }
        ]
      },
      // 保养项列表(选择保养项模板里用到)
      maintainceListData: [],
      // 保养项已经选择的列表(选择保养项模板里用到)
      maintainceDisableList: [],
      // 选择的模板
      activeTemplate: null,
      // 关联保养项表加载标识
      relationShipTableDataLoading: false,
      // 关联保养项表数据
      relationShipTableData: [],
      // 显示选择保养项modal标识
      showCreateMaintainceTempRelation: false
    };
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    // 查询保养等级
    /**
     * @description: 查询保养等级
     * @return: void
    */
    getMaintainLevel() {
      selectMaintainLevel().then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var result = {
            name: 'MaintainLevel',
            data: res.rows
          };
          this.$store.commit('setFiltersData', result);
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    /**
     * @description: 获取保养模板数据
     * @return: void
    */
    getMaintainTemplateData() {
      this.tableloading = true;
      getMaintainTemplate({
        current: this.templateTableData.page.currentPage,
        size: this.templateTableData.page.pageSize
      }).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableloading = false;
          this.templateTableData.page.total = res.total;
          this.templateTableData.data = res.rows;
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    /**
     * @description: 显示 添加/修改保养模板 modal
     * @param {Object} data 选中的保养模板
     * @return: void
    */
    showCreateMaintainceTempModal() {
      this.showCreateMaintainceTemp = true;
    },
    /**
     * @description: 显示 添加/修改保养模板 modal
     * @param {Object} data 选中的保养模板
     * @return: void
    */
    showUpdateMaintainceTempModal() {
      var row = {};

      if (!this.$refs.templateTable.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.templateTable.getRadioRecord();
      }
      if (row) {
        this.isAddItem = false;
        this.selectTemplate = row;
      }
      this.showCreateMaintainceTemp = true;
    },
    /**
     * @description: 关闭页面上的modal
     * @return: void
    */
    clickModalCancel: function() {
      this.showCreateMaintainceTempRelation = false;
      this.showCreateMaintainceTemp = false;
      this.isAddItem = true;
      this.selectTemplate = null;
    },
    /**
     * @description: 添加或修改保养模板
     * @param {Object} data 添加/修改保养模板 modal返回数据
     * @return: void
    */
    addOrUpdateMaintainTemplate(data) {
      this.clickModalCancel();
      if (data.isAdd) {
        addMaintainTemplate(data.data).then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.getMaintainTemplateData();
          } else {
            showWarningMessage(res.message);
          }
        }).catch(err => {
          showErrorNotify(TITLE.NOTIFY, err);
        });
      } else {
        updateMaintainTemplate(data.data).then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.getMaintainTemplateData();
          } else {
            showWarningMessage(res.message);
          }
        }).catch(err => {
          showErrorNotify(TITLE.NOTIFY, err);
        });
      }
    },
    toDelete() {
      var row = {};

      if (!this.$refs.templateTable.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.templateTable.getRadioRecord();
      }
      this.deleteMaintainTemplateConfirm([row]);
    },
    /**
     * @description: 是否要删除保养模板
     * @param {Array} data 要删除的数组
     * @return: void
    */
    deleteMaintainTemplateConfirm(data) {
      var ids = [];
      data.forEach(function(item) {
        ids.push(item.templateId);
      });
      this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      }).then(() => {
        if (ids.indexOf(this.activeTemplate.templateId) > -1) {
          this.activeTemplate = null;
        }
        this.deleteMaintainTemplate(ids);
      }).catch(() => {
      });
    },
    /**
     * @description: 删除保养模板
     * @param {Array} deleteItems 要删除的id 数组
     * @return: void
    */
    deleteMaintainTemplate(deleteItems) {
      deleteMaintainTemplate({ ids: deleteItems }).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.getMaintainTemplateData();
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    /**
     * @description: 展示右边保养项关联表
     * @param {Object} data 选中的保养模板
     * @return: void
    */
    handleSelectTemplate(data) {
      this.activeTemplate = data;
      this.getRelationshipItemByID(data.templateId);
    },
    /**
     * @description: 根据保养模版查询关联保养项
     * @param {int} id 选中的保养模板id
     * @return: void
    */
    getRelationshipItemByID(id) {
      this.relationShipTableDataLoading = true;
      var self = this;
      selectMaintainTemplateRelationshipItem({ templateId: id }).then(res => {
        self.relationShipTableDataLoading = false;
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          self.relationShipTableData = res.rows;
          self.maintainceDisableList = [];
          res.rows.forEach(function(item) {
            self.maintainceDisableList.push(item.maintainItemId);
          });
          self.rowDrop();
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        self.relationShipTableDataLoading = false;
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    /**
     * @description: 打开选择保养项modal
     * @return: void
    */
    showCreateMaintainceTempRelationModal() {
      getMaintainItem().then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.maintainceListData = res.rows;
          this.showCreateMaintainceTempRelation = true;
          this.$refs.maintainceTemplateRelationModal.tableDatas = JSON.parse(JSON.stringify(res.rows));
          this.$refs.maintainceTemplateRelationModal.showList = JSON.parse(JSON.stringify(res.rows));
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    /**
     * @description: 插入保养项
     * @param {Array} data 要插入的保养项数组
     * @return: void
    */
    insertMaintainceItems(data) {
      console.log(data);
      var self = this;
      data.forEach(function(item) {
        item.templateId = self.activeTemplate.templateId;
        self.relationShipTableData.push(item);
      });
      this.clickModalCancel();
    },
    /**
     * @description: 保存保养项排序
     * @return: void
    */
    submitMaintainceTempRelation() {
      var params = [];
      for (var i = 0; i < this.relationShipTableData.length; i++) {
        var item = {
          relationshipId: this.relationShipTableData[i].relationshipId,
          maintainItemId: this.relationShipTableData[i].maintainItemId,
          templateId: this.relationShipTableData[i].templateId,
          sort: i + 1
        };
        params.push(item);
      }
      updateMaintainTempRelationItem(null, params).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.getRelationshipItemByID(this.activeTemplate.templateId);
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(TITLE.NOTIFY, err);
      });
    },
    /**
     * @description: 是否要删除保养项
     * @param {Object} data 要删除的数据
     * @return: void
    */
    deleteMaintainTemplateRelationItemConfirm() {
      var row = {};

      if (!this.$refs.sortTable.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.sortTable.getRadioRecord();
      }
      this.$confirm(MSG.CONFIRM_CHOOSE_REMOVE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      }).then(() => {
        this.deleteMaintainTemplateRelationItem(row);
      }).catch(() => {
      });
    },
    /**
     * @description: 删除保养项
     * @param {Object} data 要删除的数据
     * @return: void
    */
    deleteMaintainTemplateRelationItem(data) {
      if (data.relationshipId) {
        deleteWorkshopTypeRelationshipTemplate({
          id: data.relationshipId
        }).then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            showSuccessMessage(res.message);
            this.getRelationshipItemByID(this.activeTemplate.templateId);
          } else {
            showWarningMessage(res.message);
          }
        }).catch(err => {
          showErrorNotify(TITLE.NOTIFY, err);
        });
      } else {
        var index = 0;
        for (var i = 0; i < this.relationShipTableData.length; i++) {
          if (this.relationShipTableData[i].maintainItemId === data.maintainItemId) {
            index = i;
            break;
          }
        }
        this.relationShipTableData.splice(index, 1);
      }
    },
    /**
     * @description: 初始化表拖拽
     * @return: void
    */
    rowDrop() {
      var self = this;
      this.$nextTick(() => {
        var xTable = this.$refs.sortTable;
        this.sortable = Sortable.create(xTable.$el.querySelector('.body--wrapper>.vxe-table--body tbody'), {
          handle: '.drag-btn',
          onEnd: ({ newIndex, oldIndex }) => {
            var currRow = self.relationShipTableData.splice(oldIndex, 1)[0];
            self.relationShipTableData.splice(newIndex, 0, currRow);
          }
        });
      });
    },
    /**
     * @description: 表格翻页事件
     * @param {int} currentPage 当前页码
     * @param {int} pageSize 当前每页数量
     * @return: void
    */
    handleTemplateTablePageChange(currentPage, pageSize) {
      this.templateTableData.page.currentPage = currentPage;
      this.templateTableData.page.pageSize = pageSize;
      this.getMaintainTemplateData();
    },
    /**
     * @description: table里checkbox选择事件
     * @param {Array} records 选中的表数据
     * @return: void
    */
    checkboxChangeEvent(records) {
      this.selectRecords = records;
    },
    refreshTable() {
      this.getMaintainTemplateData();
      this.getMaintainLevel();
    }
  },
  created() {
    this.getMaintainTemplateData();
    this.getMaintainLevel();
  },
  beforeDestroy() {
    if (this.sortable) {
      this.sortable.destroy();
    }
  }
};
