<template>
  <el-dialog
    class="create-maintaince-template"
    width="30%"
    :title="title"
    :visible.sync="showDialog"
    :close-on-click-modal="false"
    @closed="closeAction"
  >
    <div class="content" >
      <el-form :model="modalData" :rules="rules" ref="ruleForm" label-width="100px" class="ruleForm">
        <el-form-item :label='EQUIPMENT_DATA.TEMPLATE_NAME' prop="templateName" class="content-label">
          <el-input class="checkpoint-data" v-model="modalData.templateName" ></el-input>
        </el-form-item>
        <el-form-item :label='EQUIPMENT_DATA.MAINTANCE_LEVEL' prop="level" class="content-label">
          <el-select v-model="modalData.level"
              class="checkpoint-data"
              @change="handleSelectLevel"
              :placeholder="MSG.PLEASE_CHOOSE">
                <el-option
                  v-for="item in $store.state.filters.MaintainLevel"
                  :key="item.basicValue"
                  :label="item.basicValue"
                  :value="item">
                </el-option>
              </el-select>
        </el-form-item>
        <el-form-item :label='EQUIPMENT_DATA.MAINTANCE_SCOPE' prop="cycleName" class="content-label">
          <el-select v-model="modalData.cycleName"
              filterable
              class="checkpoint-data"
              :loading="dropdownLoading"
              :placeholder="MSG.PLEASE_CHOOSE"
              @change="handleCycleChange">
                <el-option
                  v-for="item in $store.state.filters.maintainceCycleList"
                  :key="item.cycleId"
                  :label="item.cycleName"
                  :value="item">
                </el-option>
              </el-select>
        </el-form-item>
        <el-form-item :label='EQUIPMENT_DATA.NOTES' prop="templateRemark" class="content-label">
          <el-input
            type="textarea"
            :rows="4"
            :placeholder="CONSTANTS_CODE.PLEASE_ENTER_CONTENT"
            v-model="modalData.templateRemark">
            </el-input>
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer">
      <el-button type="primary" @click="confirmAction('ruleForm')">{{confirmLabel}}</el-button>
      <el-button type="tertiary" @click="cancelAction">{{cancelLabel}}</el-button>
    </div>
  </el-dialog>
</template>
<script>
// 导入文字常量
import { EQUIPMENT_DATA, MSG, STATUS_CODE, BTN_TEXT, CONSTANTS_CODE } from '@/utils/constantsFile';
// API
import { getAllMaintainceCycle } from '@/api/maintanceManagement';
import { showErrorNotify } from '@/utils/notify';
export default {
  name: 'createMaintainceTemplateModal',
  props: {
    // 模态框名字
    title: { type: String, default: '' },
    // 取消标签
    cancelLabel: { type: String, default: BTN_TEXT.CANCEL },
    // 确认标签
    confirmLabel: { type: String, default: BTN_TEXT.SAVE },
    // 显示弹窗
    showDialog: { type: Boolean, default: false },
    // 是添加操作还是修改操作
    isAdd: { type: Boolean, default: true },
    // 数据
    data: { type: Object, default: () => {} },
    // 处理点击取消
    handleCancel: Function,
    // 处理点击确认
    handleConfirm: Function,
    // 处理点击关闭
    handleClose: Function
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      MSG: MSG,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      CONSTANTS_CODE: CONSTANTS_CODE,
      // 加载下拉数据
      dropdownLoading: false,
      // 模态框数据
      modalData: {
        levelId: null,
        level: null,
        cycleId: null,
        cycleName: null,
        templateRemark: null,
        templateName: null,
        templateId: null
      },
      rules: {
        templateName: [
          { required: true, message: EQUIPMENT_DATA.PLEASE_ENTER_TEMPNAME, trigger: 'blur' }
        ],
        level: [
          { required: true, message: EQUIPMENT_DATA.PLEASE_ENTER_LEVEL, trigger: 'change' }
        ],
        cycleName: [
          { required: true, message: EQUIPMENT_DATA.PLEASE_ENTER_CYCLENAME, trigger: 'change' }
        ]
      }
    };
  },
  watch: {
    data(val, oldVal) {
      this.initData();
    }
  },
  methods: {
    /**
     * @description: 初始化数据
     * @return: void
    */
    initData: function() {
      if (!this.isAdd) {
        this.modalData.level = this.$options.filters.formatMaintanceLevel(this.data.levelId);
        this.modalData.cycleId = this.data.cycleId;
        this.modalData.cycleName = this.data.cycleName;
        this.modalData.templateRemark = this.data.templateRemark;
        this.modalData.templateName = this.data.templateName;
        this.modalData.templateId = this.data.templateId;
      }
      this.dropdownLoading = true;
      getAllMaintainceCycle().then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.dropdownLoading = false;
          var result = {
            name: 'maintainceCycleList',
            data: res.rows
          };
          this.$store.commit('setFiltersData', result);
        }
      });
    },
    /**
     * @description: 处理选择的等级
     * @param {value} data 选中的等级
     * @return: void
    */
    handleSelectLevel: function(value) {
      this.modalData.level = value.basicValue;
      this.modalData.levelId = value.basicId;
    },
    /**
     * @description: 处理点击取消
     * @return: void
    */
    cancelAction: function() {
      if (this.handleCancel) {
        this.handleCancel();
      }
    },
    /**
     * @description: 处理点击确认
     * @return: void
    */
    confirmAction(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.handleConfirm) {
            if (this.handleConfirm) {
              for (var i = 0; i < this.$store.state.filters.MaintainLevel.length; i++) {
                if (this.$store.state.filters.MaintainLevel[i].basicValue === this.modalData.level) {
                  this.modalData.levelId = this.$store.state.filters.MaintainLevel[i].basicId;
                  break;
                }
              }
              var res = {
                isAdd: this.isAdd,
                data: this.modalData
              };
              this.handleConfirm(res);
              this.clearData();
            }
          }
        } else {
          showErrorNotify(CONSTANTS_CODE.PLEASE_ENTER_CONTENT);
          return false;
        }
      });
    },
    /**
     * @description: 处理点击关闭
     * @return: void
    */
    closeAction: function() {
      if (this.handleClose) {
        this.clearData();
        this.handleClose();
      }
    },
    /**
     * @description: 处理选择的周期
     * @param {value} data 选中的周期
     * @return: void
    */
    handleCycleChange(data) {
      this.modalData.cycleName = data.cycleName;
      this.modalData.cycleId = data.cycleId;
    },
    /**
     * @description: 清空数据
     * @return: void
    */
    clearData() {
      this.modalData = {
        level: null,
        cycleId: null,
        cycleName: null,
        templateRemark: null,
        templateName: null,
        templateId: null
      };
    }
  },
  created() {
    this.initData();
  }
};
</script>
