  margin-right: 60px;
  line-height: 30px;
}

.create-maintaince-template .input-label-2{
  margin-right: 30px;
  line-height: 30px;
}

/* common style may move out */
.create-maintaince-template .el-dialog__header{
    line-height: initial;
    text-align: initial;
    border-bottom: 1px solid grey;
}
.create-maintaince-template .el-dialog__body{
    line-height: initial;
    text-align: initial;
}
.create-maintaince-template .el-dialog__footer{
    line-height: initial;
    text-align: center;
}
