import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
// 引入类型信息组件
import addUser from '@/views/main/UserConfiguration/addUser.vue';
import {
  deviceUserRelationshipGetPageUserRelationshipDeviceByUserIdAction,
  deviceUserRelationshipDeleteByIdsAction
} from '@/api/management';
import { STATUS_CODE, TITLE, MSG, VARIABLE, BTN_TEXT, CONSTANTS_USER, CONSTANTS_DEVICE_CONFIGURATION } from '@/utils/constantsFile';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';

export default {
  props: {
    // 选中的用户数据
    currentChangeRow: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      CONSTANTS_USER: CONSTANTS_USER,
      BTN_TEXT: BTN_TEXT,
      VARIABLE: VARIABLE,
      deviceChecks: [],
      // 对话框标题
      userListTitle: CONSTANTS_USER.RELATED_LIST,
      titleName: CONSTANTS_USER.TITLE_MODIFY_USER_DIALOG,
      tableLoading: false,
      // 修改对话框是否可见，默认为false，表示不可见
      dialogVisible: false,
      // 表格初始化用到的数据
      tableDatas: [],
      // 表格分页的配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [5, 10, 20, 30, 50],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },

      // 表格列配置选项
      tableColumns: [
        // { field: 'cb', type: 'checkbox', width: '50', align: 'center' },
        {
          field: 'deviceName',
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_NAME,
          align: 'center'
        },
        {
          field: 'deviceCode',
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_CODE,
          width: '200',
          align: 'center',
          sortable: true
        },
        {
          field: 'devicePriCode',
          visible: true,
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_PRCODE,
          minWidth: '200',
          align: 'center',
          sortable: true
        },
        {
          field: 'number',
          visible: true,
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_EXTEND,
          minWidth: '200',
          align: 'center',
          sortable: true
        },
        {
          field: 'details',
          visible: true,
          title: '设备描述详情',
          minWidth: '200',
          align: 'center'
        },
        {
          field: 'mode',
          title: CONSTANTS_DEVICE_CONFIGURATION.MODE,
          width: '150',
          align: 'center'
        },
        {
          field: 'typeName',
          minWidth: '200',
          title: CONSTANTS_DEVICE_CONFIGURATION.TYPE,
          align: 'center'
        },
        {
          field: 'groupName',
          minWidth: '200',
          title: CONSTANTS_DEVICE_CONFIGURATION.GROUP_NAME,
          align: 'center'
        },
        {
          title: BTN_TEXT.OPERATION,
          width: '120',
          align: 'center',
          fixed: 'right',
          slots: {
            default: ({ row, column }) => {
              return [
                <el-button onClick={() => this.toDelete(row, column)} type="text">
                  {BTN_TEXT.DELETE}
                </el-button>
              ];
            }
          }
        }
      ]
    };
  },

  // 使用的组件
  components: {
    HeaderTitle,
    HeadOperationItem,
    VXETable,
    addUser
  },

  methods: {
    /**
     * @description: 点击批量删除
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    deteleall() {
      if (this.tableChecks.length === 0) {
        this.$message.error(MSG.PLEASE_CHOOSE_DELETE);
      } else {
        var arrdelete = [];
        this.tableChecks.map(item => arrdelete.push(item.relationshipId));
        this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
          confirmButtonText: BTN_TEXT.CONFIRM,
          cancelButtonText: BTN_TEXT.CANCEL,
          type: 'warning'
        })
          .then(() => {
            this.setDeleteChecks(arrdelete);
          })
          .catch(() => {
          });
      }
    },
    /**
     * @description: 执行批量删除的方法
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async setDeleteChecks(param) {
      try {
        const res = await deviceUserRelationshipDeleteByIdsAction({
          ids: param
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.findTableList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取批量勾选的数据
     * @param data // 勾选数据
     * @return: 无
     * @author: ZJM
     */
    tableChooseCheck(data) {
      this.tableChecks = data;
    },
    toDelete(row) {
      // 确认是否删除
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setDeleteChecks([row.relationshipId]);
        })
        .catch(() => { });
    },
    /**
     * @description: 点击“修改”按钮触发的对话框弹出事件
     * @param {object} row 行对象
     * @param {object} column 列对象
     * @return: null
     */
    showDialog(row) {
      this.dialogVisible = true;
      // 保存row修改过的数据
      this.tempRow = JSON.parse(JSON.stringify(row));
      // 保存row的初始数据，用于重置
      this.initRow = row;
    },
    /**
     * @description: 获取表格数据
     * @param {null} 无
     * @return: void
     */
    async findTableList() {
      this.tableChecks = [];
      try {
        // 播放表格加载动画
        this.tableLoading = true;
        const data = {
          userId: this.currentChangeRow.userId,
          deviceUserRelationshipEnum: 'MAINTAIN',
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };
        const res = await deviceUserRelationshipGetPageUserRelationshipDeviceByUserIdAction(data);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },

    /**
     * @description: 分页处理方法
     * @param {number} currentPage 当前页码
     * @param {number} pageSize 每页行数
     * @return: void
     */
    onTablePageChange(currentPage, pageSize) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.findTableList();
    },
    /**
     * @description: 清空选择
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    clearCheckboxRow() {
      this.$refs.xGrid.clearCheckboxRow();
      this.$refs.xGrid.selectRecords = [];
    },
    /**
     * @description: 切换组件页面
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async togglePage() {
      this.$emit('relatedDevice');
    }
  },
  created() {
    this.findTableList();
  }
};
